.overlay {
    height: 100%;
    width: 100%;
    z-index: -1;
    -webkit-clip-path: polygon(100% 0%, 100% 100%, 50% 100%, 20% 0);
            clip-path: polygon(100% 0%, 100% 100%, 50% 100%, 20% 0);
    overflow: hidden;
    border: none;
    position: fixed;
    top: 0;
    left: 0;
}

.btn {
