.theme {
    padding: 2rem;
    font-size: 1.3rem;
    left: 1rem;
    top: 75%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    position: fixed;
    background: #000;
    color: #fff;
    border-radius: 20px;
    z-index: 100;
}

.checkbox {
    opacity: 0;
    position: absolute;
}

.label {
    width: 50px;
    height: 26px;
    background-color: #777;
    display: flex;
    border-radius: 50px;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    position: relative;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    margin-left: 0.4rem;
}

.ball {
    width: 20px;
    height: 20px;
    background-color: white;
    position: absolute;
    top: 0.8rem;
    left: 0.6rem;
    border-radius: 50%;
    transition: -webkit-transform 0.2s linear;
    transition: transform 0.2s linear;
    transition: transform 0.2s linear, -webkit-transform 0.2s linear;
}


/*  target the elemenent after the label*/

.checkbox:checked+.label .ball {
    -webkit-transform: translateX(3rem);
            transform: translateX(3rem);
}
.box {
    -webkit-perspective: 150rem;
            perspective: 150rem;
    -moz-perspective: 150rem;
    position: relative;
    height: 38.5rem;
    width: 30rem;
    color: #fff;
    margin: 1rem 1rem 0 0;
    margin-bottom: 5rem;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: -webkit-transform 0.6s ease 0.5s;
    transition: transform 0.6s ease 0.5s;
    transition: transform 0.6s ease 0.5s, -webkit-transform 0.6s ease 0.5s;
    font-family: "Zen Tokyo Zoo", cursive;
    text-transform: capitalize;
}

.featured--yes {
    height: 45rem;
    width: 37rem;
}

.featured--no {
    opacity: 69%;
}

.box.not-in-view {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}

.box__side {
    box-shadow: 0.5rem 1.5rem 2rem rgba(0, 0, 0, 0.2);
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transition: all 0.8s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.box__side.front {
    background-color: #000;
}

.box__side.back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
}

.box__side.back i {
    visibility: hidden;
}

.back--1 {
    background-image: linear-gradient(to right bottom, #ffb900, #ff7730), none;
}

.back--2 {
    background-image: linear-gradient(to right, #7ed56f, #28b485), none;
}

.back--3 {
    background-image: linear-gradient(to right bottom, #299bff, #5634fa), none;
}

.box:hover .box__side.front {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.box:hover .box__side.back {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
    visibility: visible;
}

.box:hover .box__side.back i {
    visibility: visible;
}

.box__image {
    height: 35rem;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 70%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 70%, 0 100%);
    background-size: cover;
    background-position: center;
    background-blend-mode: screen;
}

.featured--yes .box__image {
    height: 40rem;
}

.image--1 {
    background-image: linear-gradient( to right bottom, rgba(255, 185, 0, 0.7), rgba(255, 119, 48, 0.7)), url(/Jarvis/static/media/pakata-goh-EJMTKCZ00I0-unsplash.3dfb06f2.jpg);
}

.image--2 {
